"use client";

import {
  Card,
  CardHeader,
  CardTitle,
  CardDescription,
  CardContent,
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import Link from "next/link";
import { ArrowRight, Bot, TrendingUp, RefreshCw } from "lucide-react";
import MagicBento from "@/components/MagicBento/MagicBento";
import FreeBanner from "@/components/FreeBanner/FreeBanner";
import { useEffect, useState } from "react";

/* -------------------------------
   TYPES
-------------------------------- */

interface WebsiteItem {
  title: string;
  status: string;
  description: string;
  severity: string;
  items?: WebsiteItem[];
}

interface JournalEntry {
  id: string;
  entry_date: string;
  content: string;
}

interface DashboardClientProps {
  user: any;
  profile: any;
  recentEntries: JournalEntry[];
}

/* -------------------------------
   COMPONENT START
-------------------------------- */

export default function DashboardClient({
  user,
  profile,
  recentEntries,
}: DashboardClientProps) {
  const isFree =
    !profile?.is_premium &&
    (profile?.plan_tier === "free" ||
      profile?.subscription_status !== "active");

  const [loadingInsights, setLoadingInsights] = useState(false);
  const [insights, setInsights] = useState<WebsiteItem[]>([]);
  const [dailyInsight, setDailyInsight] = useState("");

  /* -------------------------------
     LOAD WEBSITE INSIGHTS
  -------------------------------- */
  useEffect(() => {
    async function loadInsights() {
      try {
        const res = await fetch("/api/website/insights");
        if (!res.ok) return;

        const data = await res.json();

        const items =
          Array.isArray(data?.insights)
            ? data.insights.flatMap((i: any) =>
                Array.isArray(i.items) ? i.items : []
              )
            : [];

        setInsights(items);
      } catch (err) {
        console.error("Error loading insights:", err);
      }
    }

    loadInsights();
  }, []);

  /* -------------------------------
     AI DAILY BRIEFING (NON-STREAMING)
  -------------------------------- */
  useEffect(() => {
    async function getBriefing() {
      try {
        const res = await fetch("/api/chat/daily-briefing", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ profile }),
        });

        if (!res.ok) {
          console.error("Daily briefing request failed.");
          return;
        }

        const data = await res.json();
        if (data?.content) setDailyInsight(data.content);
      } catch (err) {
        console.error("Daily insight error:", err);
      }
    }

    if (profile) getBriefing();
  }, [profile]);

  /* -------------------------------
     RE-SCAN WEBSITE
  -------------------------------- */
  async function handleRescan() {
    try {
      setLoadingInsights(true);
      const res = await fetch("/api/website/rescan", {
        method: "POST",
      });

      if (!res.ok) {
        console.error("Re-scan failed");
        setLoadingInsights(false);
        return;
      }

      const data = await res.json();

      const items =
        Array.isArray(data?.insights)
          ? data.insights.flatMap((i: any) =>
              Array.isArray(i.items) ? i.items : []
            )
          : [];

      setInsights(items);
      setLoadingInsights(false);
    } catch (err) {
      console.error("Re-scan error:", err);
      setLoadingInsights(false);
    }
  }

  /* -------------------------------
     RENDER
  -------------------------------- */

  return (
    <div className="space-y-12 animate-fadeIn px-1 md:px-0">
      {isFree && <FreeBanner />}

      {/* PREMIUM GREETING */}
      <div className="flex flex-col gap-1 mt-4">
        <h1 className="text-3xl font-bold text-white">
          Good to see you, {profile.name || user.email}.
        </h1>
        <p className="text-white/60 text-sm">
          Hereâ€™s your personalized briefing for the day.
        </p>
      </div>

      {/* AI FEATURE CARDS */}
      <div className="grid md:grid-cols-3 gap-6 mt-4">
        <Card className="bg-white/5 border-white/10 backdrop-blur-xl hover:scale-[1.02] transition duration-300">
          <CardHeader>
            <CardTitle className="text-white flex items-center gap-2">
              <TrendingUp className="h-4 w-4 text-brandOrange" />
              Todayâ€™s Briefing
            </CardTitle>
            <CardDescription className="text-white/60">
              One insight to guide your momentum.
            </CardDescription>
          </CardHeader>
          <CardContent>
            <p className="text-white/80 leading-relaxed">
              {dailyInsight || "Loading your briefing..."}
            </p>
          </CardContent>
        </Card>

        <Link href="/mentor">
          <Card className="bg-white/5 border-white/10 backdrop-blur-xl hover:scale-[1.02] transition duration-300 cursor-pointer">
            <CardHeader>
              <CardTitle className="text-white flex items-center gap-2">
                <Bot className="h-4 w-4 text-brandBlueLight" />
                Ask Your Mentor
              </CardTitle>
              <CardDescription className="text-white/60">
                Strategy, clarity, execution â€” on demand.
              </CardDescription>
            </CardHeader>
            <CardContent>
              <Button className="bg-brandBlue text-white hover:bg-brandBlueLight">
                Open Mentor <ArrowRight className="ml-2 h-4 w-4" />
              </Button>
            </CardContent>
          </Card>
        </Link>

        <Card className="bg-white/5 border-white/10 backdrop-blur-xl hover:scale-[1.02] transition duration-300">
          <CardHeader>
            <CardTitle className="text-white flex items-center gap-2">
              <TrendingUp className="h-4 w-4 text-brandOrange" />
              Your Momentum
            </CardTitle>
            <CardDescription className="text-white/60">
              A look at your progress.
            </CardDescription>
          </CardHeader>
          <CardContent>
            <p className="text-white/80">
              {recentEntries?.length
                ? `Youâ€™ve journaled ${recentEntries.length} times recently.`
                : "Your momentum starts with your first entry."}
            </p>
          </CardContent>
        </Card>
      </div>

      {/* WEBSITE ANALYZER */}
      <div className="space-y-4">
        <h2 className="text-xl font-semibold text-white flex items-center gap-2">
          Website Analyzer
          <Button
            variant="outline"
            className="text-white border-white/20 bg-white/5 hover:bg-white/10"
            onClick={handleRescan}
          >
            <RefreshCw className="h-4 w-4 mr-1" />
            {loadingInsights ? "Scanning..." : "Re-scan"}
          </Button>
        </h2>

        {insights.length === 0 ? (
          <p className="text-white/60">No insights yet.</p>
        ) : (
          <div className="grid gap-4">
            {insights?.map((item: WebsiteItem, index: number) => (
              <Card
                key={index}
                className="bg-white/5 border-white/10 backdrop-blur-xl"
              >
                <CardHeader>
                  <CardTitle className="text-white">{item.title}</CardTitle>
                  <CardDescription className="text-white/60">
                    Severity: {item.severity}
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <p className="text-white/70">{item.description}</p>
                </CardContent>
              </Card>
            ))}
          </div>
        )}
      </div>

      {/* MAGIC BENTO */}
      <MagicBento />

      {/* JOURNAL */}
      <div className="space-y-4 mt-10">
        <h2 className="text-xl font-semibold text-white">
          Recent Journal Entries
        </h2>

        {recentEntries?.length === 0 ? (
          <p className="text-white/50">No entries yet.</p>
        ) : (
          recentEntries.map((entry: JournalEntry) => (
            <div
              key={entry.id}
              className="p-4 rounded-xl bg-white/5 border border-white/10 text-white"
            >
              <p className="text-sm text-white/60">{entry.entry_date}</p>
              <p className="mt-1">{entry.content}</p>
            </div>
          ))
        )}
      </div>
    </div>
  );
}
